.square {
  background : #fff;
  border : #999 solid 1px;
  float : left;
  font-size : 50px;
  font-weight : bold;
  margin-right : -1px;
  margin-top : -1px;
  padding : 0;
  text-align : center;
  width : 80px;
  line-height : 80px;
  height : 80px;
  display : inline-block;
  text-decoration : none;
  color : #fff;
  font-weight : bold;
  background-color : #538fbe;
  margin : 10px;
  border : #2d6898 solid 1px;
  border-radius : 5px;
  text-shadow : 0 -1px 0 rgb(0, 0, 0, 0.5);
  box-shadow : 0 6px 0 #2b638f, 0 3px 15px rgb(0, 0, 0, 0.4), 0 1px 0 rgb(255, 255, 255, 0.3) inset, 0 0 3px rgb(255, 255, 255, 0.5) inset;
  }

  /* Board */
  .board-row:after {
  clear : both;
  content : "";
  display : table;
  }

  /* Game */
  .game {
  position : absolute;
  top : 50%;
  left : 50%;
  transform : translate(-50%,-50%);
  display : flex;
  flex-direction : row;
  margin : 0 auto;
  }

  /* Game Info */
  ul {
  padding-left : 30px;
  }

  .game-info {
  margin-left : 20px;
  }

  .status {
  margin-bottom : 10px;
  }


